export default {
	data() {
		return {
			top_active: 0,
			top_data: 0,
		}
	},
	onLoad(e) {
		console.log(e)
		this.top_active = e.top_active;
		this.top_data = e.top_data;
	},
	onShow() {

	},
	methods: {
		changeTop(e) {
			if (this.top_active != e.currentTarget.dataset.index) {
				if (this.top_active == 0) {
					this.top_active = 1
					this.top_data = 0
				} else {
					this.top_active = 0;
					this.top_data = 1
				}
			}
		}
	}
}
